import React, { Component } from 'react';

class DeadpoolFact extends Component {
    render(){
        return(
            <div>
                <h1 style={HeadStyle}>Deadpool Facts</h1>
                <h1 style={HeadStyle}>Hello, {this.props.name}</h1>
                <hr/>
                <div>
    
                    <div style={factStyle}>
                    {this.props.title}
                        
                    </div>
                </div>
            </div>
        );
    }

   
}

 

const HeadStyle ={
        fontFamily: "'Press Start 2P', cursive",
        textShadow: '7px 3px 7px  tomato',
        letterSpacing: '3px',
        marginTop: "5px",
        textAlign:'center'
       
}
const factStyle ={
    textAlign:"center"
}
export default DeadpoolFact;