import React ,{Component}from 'react';
import Navbar from './component/Navbar.js';
import DeadpoolFact from './component/DeadpoolFact.js';



class App extends Component {
   
   

   

    render(){
        return(
            <div>
                <Navbar/>
                <DeadpoolFact name ={element.name}
                    class ={element.class}
                    title={title}
                    
                />
            
                                
                
            </div>
        )
    }
    

 
}
const element ={name:'Prakash',class:'12'}

const list = [
    {title:"A Simple Man",
    img:"https://th.bing.com/th/id/OIP.-4nkXQYN05ljzfJez_azbgHaEK?pid=Api&rs=1",
    fact:"In fact, that’s why his costume was red. He was modeled after Deathstroke, but also because the artists were inspired by Spider-Man (although Deadpool is convinced it’s the other way around). He was also conceptualized as a character that would be easy to draw so that the artists could meet their deadlines faster."},
    {title:"A Complicated Web",
    img:"https://www.theedgesusu.co.uk/wp-content/uploads/2016/06/deadpool-2.jpg",
    fact:"Speaking of Spider-Man, of all the enduring relationships in Marvel history, there is none sweeter than Deadpool’s undying desire for Spider-Man to be his best friend. Spider-Man isn’t quite there yet, but Deadpool is convinced he’ll break him down. We’re rooting for these two kids."},
    {title:"Classic Good News Bad News",
    img:"https://th.bing.com/th/id/OIP.QvustG-XbRXgezehrNCKkQHaDt?pid=Api&rs=1",
    fact:"Deadpool got his powers after he joined Weapon-X in an attempt to obtain the healing factor that would cure him of his cancer. While the healing factor prevented him from dying (that’s good), it also advanced his cancer and left him horribly disfigured (that’s bad)."}];

const title =list.map((list)=><div>
  
    <h1>{list.title}</h1>
     <img src={list.img} alt="hjhj" width='75%'/>
    <p>{list.fact}</p>
    <hr/>
    </div>
)

const backColor ={
    backgroundColor:"red",
}

export default App;