import React, { Component } from 'react';


class Navbar extends Component {
    render(){
        return(
            <div>
                <nav style={style}>
                    <table>
                        <tr>
                        <td >
                        <img style={imgStyle} src="http://fc02.deviantart.net/fs71/i/2013/359/a/4/deadpool_logo_1_fill_by_mr_droy-d5q6y5u.png" alt="Deadpool logo"/>
                        </td >
                        <td >
                        <h1 style={fontStyle}>DeadPool Fanpage</h1>
                        </td>
                        </tr>
                    </table>
                    
                </nav>
            </div>
        );
    }
}


const style = {
    border: 'solid',
    borderColor: '#fe6545',
    backgroundColor:'#fffc16',
    display: 'flex',
    height: "100px",
    padding: '10px' 
    
       
}


const imgStyle ={
    width: 'auto',
    maxWidth:"100px",
    height: 'auto',
    maxHeight:"100px",
    
    margin: '0px',
    padding:'0px'
   
}
    

const fontStyle ={
    color: 'Red',
    margin: '0px',
    padding: '10px',
    fontSize: "35px",
    flex : 2,
    fontStyle:"oblique",
    fontFamily: "'Sansita Swashed', cursive"   
    
}
export default Navbar;